import React, { Component } from 'react';
import {
    Card,  CardImg, CardTitle, CardText, 
    CardSubtitle, CardBody,
} from 'reactstrap';
import axios from 'axios';
import AppHeader from '../common/AppHeader';


import Detail from './Detail';

class DetailPage extends Component {
    constructor(props) {
        super(props);
        this.state = {

            detail: {
                  
                    
            }
                
                    
                
            


            

        };
    }
    componentDidMount() {
        axios.get(`http://reduxblog.herokuapp.com/api/posts/${this.props.match.params.id}`
        )
            .then(res => {
               this.setState({
                    detail: res.data
                })
                console.log(res)
            });
    }
    render() {
        console.log(this.props.match.params.id)
        const {title, categories, content} = this.state.detail
        
        return (
            <div>
<AppHeader />
                <Detail />
                <Card >
                            <CardImg top width="100%" height="500px" src={categories} alt="Card image cap" />
                           
                           <CardBody>
                                <CardTitle>{title}</CardTitle>
                                <CardSubtitle>{content}</CardSubtitle>
                                <CardText>This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</CardText>
                                
                               
                                
                            </CardBody>
                          

                        </Card>

            </div>
         ) ;
         
      
    }

}

export default DetailPage;